"use client";

import { useEffect, useState } from "react";
import { Card, CardContent } from "@/components/ui/card";
import { motion } from "framer-motion";

interface TimeLeft {
  days: number;
  hours: number;
  minutes: number;
  seconds: number;
}

const units: { key: keyof TimeLeft; label: string }[] = [
  { key: "days", label: "Days" },
  { key: "hours", label: "Hours" },
  { key: "minutes", label: "Minutes" },
  { key: "seconds", label: "Seconds" },
];

export function CountdownTimer() {
  const [timeLeft, setTimeLeft] = useState<TimeLeft>({
    days: 0,
    hours: 0,
    minutes: 0,
    seconds: 0,
  });

  useEffect(() => {
    const launchDate = new Date(
      process.env.NEXT_PUBLIC_LAUNCH_DATE || "2024-12-31T00:00:00Z"
    ).getTime();

    const timer = setInterval(() => {
      const now = Date.now();
      const distance = launchDate - now;

      if (distance > 0) {
        setTimeLeft({
          days: Math.floor(distance / (1000 * 60 * 60 * 24)),
          hours: Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)),
          minutes: Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60)),
          seconds: Math.floor((distance % (1000 * 60)) / 1000),
        });
      } else {
        setTimeLeft({ days: 0, hours: 0, minutes: 0, seconds: 0 });
      }
    }, 1000);

    return () => clearInterval(timer);
  }, []);

  return (
    <div className="mx-auto grid w-full max-w-2xl grid-cols-2 gap-3 sm:gap-4 md:grid-cols-4">
      {units.map(({ key, label }, i) => (
        <motion.div
          key={key}
          initial={{ opacity: 0, scale: 0.95 }}
          animate={{ opacity: 1, scale: 1 }}
          transition={{ delay: 0.1 + i * 0.05, duration: 0.3 }}
        >
          <Card className="card-glow h-full border-primary/25 bg-card/60 transition-all duration-300 hover:border-primary/40 hover:bg-card/80 dark:bg-card/40">
            <CardContent className="flex flex-col items-center justify-center px-4 py-5 sm:px-6 sm:py-6">
              <span
                className="mb-1 font-mono text-3xl font-bold tabular-nums text-primary sm:text-4xl"
                aria-hidden
              >
                {timeLeft[key].toString().padStart(2, "0")}
              </span>
              <span className="text-xs font-medium uppercase tracking-widest text-muted-foreground">
                {label}
              </span>
            </CardContent>
          </Card>
        </motion.div>
      ))}
    </div>
  );
}
